import request from '../../utils/request.js'
module.exports = {
	data(){
		return {
			
			userInfo: {},
			personalList: [
				{
					name: '我的订单',
					icon: 'icon-dingdan11'
				},
				{
					name: '我的拼团',
					icon: 'icon-pintuandingdan'
				},
				{
					name: '领红包',
					icon: 'icon-tubiaolunkuo-1'
				},
				{
					name: '我的积分',
					icon: 'icon-jifen3'
				},
				{
					name: '地址管理',
					icon: 'icon-dizhiguanli'
				},
				{
					name: '账号安全',
					icon: 'icon-dingdan11'
				},
				{
					name: '联系客服',
					icon: 'icon-zhanghaoanquan'
				},
				{
					name: '用户反馈',
					icon: 'icon-tubiaolunkuo-'
				},
				{
					name: '帮助中心',
					icon: 'icon-bangzhuzhongxin'
				},
				{
					name: '退还/售后',
					icon: 'icon-shouhou'
				}
			]
		}
	},
	mounted(){
		// 获取用户基本信息
		wx.getUserInfo({
			success: (res) => {
				this.userInfo = res.userInfo
			}
		})
		
		// 获取用户唯一标识openId
		// 1. 获取用户的登录凭证
		wx.login({
			success:async (res) => {
				console.log(res)
				let code = res.code;
				// 2. 将code发送请求给自己的服务器
				let token = await request('/getOpenId', {code});
				// 3. 将用户的登录态存放至本地
				wx.setStorage({
					key: 'token',
					data: token
				})
			}
		})
	},
	methods: {
		toLogin(){
			wx.navigateTo({
				url: '/pages/login/login'
			})
		},
		// 测试验证用户身份
		async getUserInfoTest(){
			let token = wx.getStorageSync('token');
			let result = await request('/getUserInfo', {token});
			console.log(result)
		}
	},
}
